import poupleInput from "@/components/pouple-input";//输入确定框
import uniPagination from '@/components/uni-pagination/uni-pagination.vue'//分页组件
export default {
	components: {
		poupleInput,uniPagination
	},
	data() {
		return {
			index:1,
			info:"姓名/身份证号",
			date:this.$custom.getYMD(),
			fxList:[],//放行记录
			shList:[],// 审核记录
			pageSize:20,//每页显示的数据量
			total:20,//总页数
			currentPage:1,//当前页数
			canFetch:false,// 判断是否有数据，点击下一页时会判断
			
			queryData:{
				info:"",
				date:"",
				adminOpenId:this.$store.state.openId,
				limit : 20,
				page : 1,//请求参数
			}
		};
	},
	onLoad() {
		this.loadHistoryRecord();
	},
	methods: {
		// 初始放行记录
		async loadHistoryRecord(){
			this.loading();
			if(this.queryData.info == '姓名/身份证号'){
				this.queryData.info = "";
			}
			let result = await this.$request.post(this.$api.getRegisterLog,this.queryData);
			if(result.appCode == 1){
				result.data.records.forEach(data=>{
					if(!this.$custom.isEmpty(data.temp) && parseFloat(data.temp)>=37.4){
						data.wd = true;
					}else{
						data.wd = false;
					}
					let str = data.zjhm.slice(0,2);
					data.zjhmRed = str == "42"?true:false;
				});
				this.canFetch = true;
				this.total = result.data.total;
				this.fxList = result.data.records;
			}else{
				this.$refs['Message'].error(result.appMsg);
			}
			wx.hideLoading();
		},
		// 审核记录
		async loadHistoryExamine(){
			this.loading();
			if(this.queryData.info == '姓名/身份证号'){
				this.queryData.info = "";
			}
			let result = await this.$request.post(this.$api.getCheckLog,this.queryData);
			if(result.appCode == 1){
				result.data.records.forEach(data=>{
					let str = data.zjhm.slice(0,2);
					data.zjhmRed = str == "42"?true:false;
				});
				this.canFetch = true;
				this.total = result.data.total;
				this.shList = result.data.records;
			}else{
				this.$refs['Message'].error(result.data.appMsg);
			}
			wx.hideLoading();
		},
		// tab切换
		tabClick(index){
			this.index = index;
			// 切换时清除输入框数据
			this.info = "姓名/身份证号"; 
			// 清除分页为起始页
			this.currentPage = 1;
			this.queryData = {
				info:this.info,
				date:this.date,
				adminOpenId:this.$store.state.openId,
				limit : this.pageSize,
				page : this.currentPage
			}
			
			// 发起请求
			this.queryRecord();
		},
		// 选择时间事件
		bindChange(e){
			this.date = e.detail.value;
			// 清除分页为起始页
			this.currentPage = 1;
			// 请求数据
			this.queryData = {
				info:this.info,
				date:this.date,
				adminOpenId:this.$store.state.openId,
				limit : this.pageSize,
				page : this.currentPage
			}
			// 发起请求
			this.queryRecord();
		},
		// 点击弹出panel输入框
		clickInput(){
			this.$refs.poupleInput.show();
		},
		// 子组件传值确认按钮
		poupleInput(info){
			this.$refs.poupleInput.hide();
			this.info = info;
			// 清除分页为起始页
			this.currentPage = 1;
			this.queryData = {
				info:info,
				date:this.date,
				adminOpenId:this.$store.state.openId,
				limit : this.pageSize,
				page : this.currentPage
			}
			// 发起请求
			this.queryRecord();
		},
		// 区分请求
		queryRecord(){
			if(this.index == 1){
				// 加载放行记录
				this.loadHistoryRecord();
			}else{
				// 加载审核记录
				this.loadHistoryExamine();
			}
		},
		// 点击分页
		jumpPage(e){
			// 如果有数据
			if(this.canFetch){
				this.canFetch = false;
				// 当前页码
				this.currentPage = e.current;
				this.queryData.page = this.currentPage;
				// 区分审核记录
				if(this.index == 1){
					// 加载放行记录
					this.loadHistoryRecord();
				}else{
					// 加载审核记录
					this.loadHistoryExamine();
				}
			}
		},
		// 显示load
		loading(){
			uni.showLoading({
				mask:true,
			    title: "正在加载中"
			});
		}
		
	}
}
